@import url(https://fonts.googleapis.com/css?family=Cinzel+Decorative|Open+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    background-image: url(/portfolio/static/media/space-background.b3778cdd.jpg);
    background-size: cover;
    background-position: center;

    padding: 10vh 5vw;
}

.Header .Content {
    color: antiquewhite;
    display: flex;
    justify-content: space-between;
    width: 100%;
}

.Header .Content .Name {
    font-family: 'Cinzel Decorative', sans-serif;
    font-size: 25px;
}


.Hero{
    background-image: url(/portfolio/static/media/sky-background.de9c9968.jpg);
    background-size: cover;
    background-position: bottom;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    
    padding: 10vh 5vw;
}

.Hero img{
    padding: 5em;
    border-radius: 25px;
}

.Hero-message{
    display: inline-block;
    font-family: 'Cinzel Decorative', sans-serif;
    padding: 0.5em;
    background-color: rgba(255, 255, 255, 0.582);
    border-radius: 25px;
    font-size: 50px;
}

.Hero-body{
    display: inline-block;
    padding: 0.5em;
    font-family: 'Open Sans', sans-serif;
    background-color: rgba(255, 255, 255, 0.582);
    border-radius: 25px;
    font-size: 25px;
    text-align: center;
}

.Hero-body p{
    padding-bottom: 5em;
}
.Footer {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;

    background-image: url(/portfolio/static/media/forest-background.6a4c4eef.jpg);
    background-size: cover;
    background-position: bottom;

    padding: 10vh 5vw;
}

.Footer .Content {
    color: antiquewhite;
}

.Footer .foot-text {
    display: flex;
    justify-content: space-between;
}
.Info {
    background-image: url(/portfolio/static/media/plane-background.6a7f63da.jpg);
    background-size: cover;
    background-position: center;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    padding: 10vh 5vw;
}

.Heading {
    display: inline-block;
    font-family: 'Cinzel Decorative', sans-serif;
    padding: 0.5em;
    background-color: rgba(255, 255, 255, 0.582);
    border-radius: 25px;
    font-size: 30px;
}

.MiniHead {
    display: flex;
    padding: 0.5em;
    font-family: 'Open Sans', sans-serif;
    background-color: rgba(255, 255, 255, 0.582);
    border-radius: 25px;
    font-size: 25px;
    text-align: center;
    margin-bottom: 3em;
}

.Contact {
    padding-top: 5em;
}

.Body {
    justify-content: left;
    align-items: left;
}

.body {
    height: 500px;
    width: 500px;
}

.Input {
    justify-content: left;
    align-items: left;
    
}

.subject {
    margin-bottom: 1em;
}

.Icons {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-bottom: 3em;
}
